import polyvLive from '@/utils/polyv/polyvlive.js';
export default {
	data() {
		return {
			video:{}
		}
	},
	methods: {
		init(data) {
		  var liveVideoContext = uni.createVideoContext('polyvLiveVideo');
		  polyvLive.getVideo({
		    uid: data.uid,
		    vid: data.vid,
		    videoContext: liveVideoContext,
		    success: videoInfo => {
		      this.setPlayerSrc(videoInfo);
		    },
		    onApiStatus: status => {
				this.$emit('apiStatus',status)
		    },
		    onStartLive: () => {
		      this.setNewPlayerSrc();
		    }
		  
		  });
		},
					
		setNewPlayerSrc() {
		  polyvLive.getNewVideo((videoInfo) => {
		    this.setPlayerSrc(videoInfo);
		  });
		},
					
		setPlayerSrc(videoInfo) {
						  
		  this.video = {
		      src: videoInfo.flvSrc,
		      poster: videoInfo.poster
		   }
		}
	}
}
